"use client";

import { useEffect, useState } from "react";
import axios from "axios";
import Chip from "@/components/Chip";
import CategoryCard from "@/components/CategoryCard";

export default function HomePage() {
  const [catalog, setCatalog] = useState<any>(null);

  // category selection
  const categories = ["Road Sign", "Road Marking", "Traffic Calming", "Custom Query"];
  const [selectedCategories, setSelectedCategories] = useState<string[]>([]);

  // custom query mode
  const [customText, setCustomText] = useState("");
  const [customList, setCustomList] = useState<string[]>([]);

  // store data from each category card
  const [cardData, setCardData] = useState<Record<string, any>>({});

  // final prompt
  const [finalPrompt, setFinalPrompt] = useState("");
  const [aiOutput, setAiOutput] = useState("");
  const [loading, setLoading] = useState(false);

  // Load catalog from backend
  useEffect(() => {
    axios.get("http://localhost:8000/catalog").then(res => {
      setCatalog(res.data);
    });
  }, []);

  // Update final prompt whenever selections change
  useEffect(() => {
    const parts: string[] = [];

    // structured categories
    for (const cat of Object.keys(cardData)) {
      const rows = cardData[cat] || [];
      for (const row of rows) {
        if (row.issue && row.subtypes.length > 0) {
          const sub = row.subtypes.join(" | ");
          const speed = row.speed ? ` on ${row.speed} road` : "";
          parts.push(`${row.issue} ${sub}${speed}.`);
        }
      }
    }

    // custom queries
    customList.forEach(q => parts.push(`Custom: ${q}`));

    setFinalPrompt(parts.join("\n"));
  }, [cardData, customList]);

  const toggleCategory = (cat: string) => {
    if (selectedCategories.includes(cat)) {
      setSelectedCategories(selectedCategories.filter(c => c !== cat));
    } else {
      setSelectedCategories([...selectedCategories, cat]);
    }
  };

  const addCustomQuery = () => {
    if (customText.trim().length > 0) {
      setCustomList([...customList, customText.trim()]);
      setCustomText("");
    }
  };

  const restartAll = () => {
    setSelectedCategories([]);
    setCardData({});
    setCustomText("");
    setCustomList([]);
    setFinalPrompt("");
    setAiOutput("");
    setLoading(false);
  };

  const submitToAI = async () => {
    if (!finalPrompt.trim()) return;
    setLoading(true);

    try {
      const res = await axios.post("http://localhost:8000/process", {
        text: finalPrompt,
        top_k: 4,
      });
      setAiOutput(res.data.answer);
    } catch (e) {
      console.error(e);
      setAiOutput("Error contacting backend.");
    }
    setLoading(false);
  };

  return (
    <div className="min-h-screen flex justify-center pt-10 px-4">
      <div className="w-full max-w-3xl">

        {/* HEADER */}
        <header className="text-center mb-10">
          <h1 className="text-3xl font-bold">RoadMarshal AI</h1>
          <p className="text-lg font-semibold text-zinc-700 dark:text-zinc-200 mt-3">
            Hi â€” how can I assist you today?
          </p>
        </header>

        {/* CATEGORY SELECTION */}
        <div className="flex flex-wrap gap-3 justify-center mb-8">
          {categories.map(cat => (
            <Chip
              key={cat}
              label={cat}
              selected={selectedCategories.includes(cat)}
              onClick={() => toggleCategory(cat)}
            />
          ))}
        </div>

        {/* SHOW CARDS FOR EACH SELECTED CATEGORY */}
        <div className="space-y-8">

          {/* Structured categories */}
          {selectedCategories
            .filter(cat => cat !== "Custom Query")
            .map(cat => (
              <CategoryCard
                key={cat}
                category={cat}
                catalog={catalog}
                onChange={(rows: any) => {
                  setCardData(prev => ({ ...prev, [cat]: rows }));
                }}
              />
            ))}

          {/* CUSTOM QUERY */}
          {selectedCategories.includes("Custom Query") && (
            <div className="bg-white dark:bg-zinc-900 p-5 border rounded-lg shadow-sm space-y-4">
              <h3 className="text-lg font-semibold">Custom Query</h3>

              <textarea
                className="w-full p-3 border rounded bg-transparent min-h-[120px]"
                placeholder="Describe your custom issue..."
                value={customText}
                onChange={(e) => setCustomText(e.target.value)}
              />

              <button
                onClick={addCustomQuery}
                className="px-4 py-2 bg-blue-600 text-white rounded"
              >
                Add Query
              </button>

              {customList.length > 0 && (
                <div className="mt-4 space-y-2">
                  {customList.map((q, i) => (
                    <div
                      key={i}
                      className="bg-zinc-100 dark:bg-zinc-800 p-3 rounded text-sm"
                    >
                      {q}
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>

        {/* FINAL PREVIEW */}
        {finalPrompt && (
          <div className="mt-10 bg-white dark:bg-zinc-900 border rounded-lg p-5 shadow-sm">
            <h3 className="text-lg font-semibold mb-3">Final Query Preview</h3>

            <pre className="whitespace-pre-wrap bg-zinc-100 dark:bg-zinc-800 p-4 rounded text-sm">
              {finalPrompt}
            </pre>

            <div className="flex justify-end mt-4 gap-3">
              <button
                onClick={submitToAI}
                className="px-4 py-2 bg-emerald-600 text-white rounded"
              >
                {loading ? "Processing..." : "Submit to AI"}
              </button>

              <button
                onClick={restartAll}
                className="px-4 py-2 bg-zinc-300 dark:bg-zinc-700 rounded"
              >
                Restart
              </button>
            </div>
          </div>
        )}

        {/* AI OUTPUT */}
        {aiOutput && (
          <div className="mt-10 bg-white dark:bg-zinc-900 border rounded-lg p-5 shadow-sm">
            <h3 className="text-lg font-semibold mb-3">AI Output</h3>
            <div className="prose dark:prose-invert max-w-none">
              <div dangerouslySetInnerHTML={{ __html: aiOutput.replace(/\n/g, "<br/>") }} />
            </div>
          </div>
        )}

      </div>
    </div>
  );
}
